html, body {
  height: 100%;
  margin: 0;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 1.8rem;
  background-color: #f7f7f7;
  display: flex;
  flex-direction: column;
  overflow: -moz-scrollbars-vertical;
  overflow-x: hidden;
  overflow-y: auto;
}

.main-content {
  display: flex;
  flex-direction: column;
}

.main-content, body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  justify-content: space-between;
}

a {
  text-decoration: none;
  color: #0360b8;
}

img {
  height: auto;
}

h1, h2, h3 {
  color: #024a8d;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

.header, .panel__header {
  padding: 1.5rem 2rem;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  margin: 0;
  transition: 0.5s;
  background-color: #FFFFFF;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
}
.header__contenedor {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__contenido--iqz h3 {
  margin: 0;
  color: #0360b8;
}
.header__navegacion {
  background-color: #ecebeb;
  padding: 0.4rem 0.6rem;
  border-radius: 1rem;
  display: flex;
  align-items: center;
  text-align: center;
  gap: 0.5rem;
}
.header__navegacion .header__enlace {
  background-color: #0e267c;
  padding: 0.5rem;
  border-radius: 0.5rem;
  color: #eee;
}
.header__navegacion .header__submit {
  all: unset;
  cursor: pointer !important;
  padding: 0.5rem;
  border-radius: 0.5rem;
}
.header__navegacion .header__submit:hover {
  background-color: rgb(220.15, 220.15, 220.15);
}
.header__navegacion .login {
  background-color: #f66822;
  color: #fff;
  border-radius: 0.5rem;
}
.header__logo {
  margin: 0;
}
.header__menu-hambugesa {
  cursor: pointer;
}

.panel__header {
  background-color: transparent;
  box-shadow: none;
  padding: 0.5rem 0.8rem;
}
.panel__contenedor {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #FFFFFF;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  padding: 1rem 2rem;
  border-radius: 0.6rem;
}
.panel__contenedor a h3 {
  margin: 0;
}
.panel__menu-hamburguesa {
  cursor: pointer;
  transition: 0.4s ease-in-out;
}
@media (min-width: 768px) {
  .panel__menu-hamburguesa {
    display: none;
  }
}

.footer {
  margin-top: auto;
}
.footer__copyright {
  background-color: rgb(2.5909090909, 82.9090909091, 158.9090909091);
  margin: 3rem 0 0 0;
  padding: 2rem 0;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 900;
  color: #FFFFFF;
}
.footer__copyright--regular {
  font-weight: 400;
}

.alerta {
  padding: 1.5rem 1.5rem 1.5rem 3rem;
  text-transform: uppercase;
  margin: 1rem 0;
  font-size: 1.3rem;
  font-weight: 700;
}
.alerta__error, .alerta__pas {
  background-color: rgb(251.9420600858, 210.5579399142, 210.5579399142);
  border-left: #da0f0f solid 0.5rem;
  color: #da0f0f;
}
.alerta__exito {
  background-color: rgb(151, 255, 178.801980198);
  border-left: #00ca36 solid 0.5rem;
  color: rgb(0, 100, 26.7326732673);
}
.swal2-container.swal2-center > .swal2-popup {
  min-width: 40%;
  height: 80%;
}

.swal2-title {
  font-size: 3rem !important;
}

.swal2-html-container {
  font-size: 1.8rem !important;
}

.swal2-confirm.swal2-styled {
  font-size: 1.4rem !important;
}

.cancel-button-class {
  font-size: 1.2rem !important;
  padding: 8px 18px;
}

.formulario {
  display: flex;
  flex-direction: column;
  background: rgba(234, 237, 244, 0.5);
  padding: 2rem;
  border-radius: 1.5rem;
}
@media (min-width: 768px) {
  .formulario {
    padding: 3rem;
  }
}
.formulario__fieldset {
  border: none;
  margin-top: 1rem;
  padding: 0;
}
.formulario__legend {
  color: #64748B;
  font-size: 2.2rem;
  margin-bottom: 2rem;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1.5rem 0;
}
.formulario__contenedor-icono {
  display: flex;
  border-radius: 0.5rem;
  overflow: hidden;
}
.formulario__icono {
  background-color: #1E293B;
  width: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.formulario__icono i {
  color: #FFFFFF;
  font-size: 2rem;
}
.formulario__label {
  display: block;
  font-size: 1.8rem;
  color: rgba(26, 27, 21, 0.8);
}
.formulario__input, .formulario__input--sociales, .formulario__select--sociales, .formulario__select {
  padding: 1rem;
  border: 1px solid #c4c4c4;
  border-radius: 0.8rem;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.21);
}
.formulario__input:focus-visible, .formulario__input--sociales:focus-visible, .formulario__select--sociales:focus-visible, .formulario__select:focus-visible {
  outline: 2px solid #f66822;
}
.formulario__input:focus-visible, .formulario__input--sociales:focus-visible, .formulario__select--sociales:focus-visible, .formulario__select:focus-visible {
  background-color: rgb(247.35, 247.35, 247.35);
}
.formulario__input--file, .formulario__select--file {
  border: none;
}
.formulario__input--sociales, .formulario__select--sociales {
  flex: 1;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.formulario__radio {
  display: flex;
  gap: 2rem;
}
.formulario__texto {
  margin-top: 3rem;
}
.formulario__imagen {
  max-width: 20rem;
}
.formulario__listado {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  margin-top: 1.5rem;
}
.formulario__listado > li {
  background-color: #0360b8;
  padding: 1rem;
  border-radius: 0.5rem;
  font-size: 1.4rem;
  color: #FFFFFF;
  font-weight: 700;
}
.formulario__submit {
  background-color: #f66822;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  width: auto;
  align-self: center;
  border-radius: 0.5rem;
}
.formulario__submit:hover {
  background: rgb(220.0391304348, 78.6565217391, 8.9608695652);
  cursor: pointer;
}
.formulario__submit--registrar, .formulario__submit--full {
  width: 100%;
}

.contenedor-table {
  width: 95%;
  margin: 0 auto;
}

.img-plantilla {
  max-width: 15rem;
}

.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 260px;
  background: #0360b8;
  z-index: 100;
  transition: all 0.5s ease;
}

.sidebar.close {
  width: 78px;
}

.sidebar .logo-details {
  height: 60px;
  width: 100%;
  display: flex;
  align-items: center;
}

.sidebar .logo-details i {
  cursor: pointer;
  font-size: 30px;
  color: #fff;
  height: 50px;
  min-width: 78px;
  text-align: center;
  line-height: 50px;
}

.sidebar .logo-details .logo_name {
  font-size: 22px;
  color: #fff;
  font-weight: 600;
  transition: 0.3s ease;
  transition-delay: 0.1s;
}

.sidebar.close .logo-details .logo_name {
  transition-delay: 0s;
  opacity: 0;
  pointer-events: none;
}

.sidebar .nav-links {
  height: 100%;
  padding: 30px 0 150px 0;
  overflow: auto;
}

.sidebar.close .nav-links {
  overflow: visible;
}

.sidebar .nav-links::-webkit-scrollbar {
  display: none;
}

.sidebar .nav-links li {
  position: relative;
  list-style: none;
  transition: all 0.4s ease;
}

.sidebar .nav-links li:hover {
  background: rgb(3.8181818182, 122.1818181818, 234.1818181818);
}

.sidebar .nav-links li .iocn-link {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.sidebar.close .nav-links li .iocn-link {
  display: block;
}

.sidebar .nav-links li i {
  height: 50px;
  min-width: 78px;
  text-align: center;
  line-height: 50px;
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.sidebar .nav-links li.showMenu i.arrow {
  transform: rotate(-180deg);
}

.sidebar.close .nav-links i.arrow {
  display: none;
}

.sidebar .nav-links li a {
  display: flex;
  align-items: center;
  text-decoration: none;
}

.sidebar .nav-links li a .link_name {
  font-size: 18px;
  font-weight: 400;
  color: #fff;
  transition: all 0.4s ease;
}

.sidebar.close .nav-links li a .link_name {
  opacity: 0;
  pointer-events: none;
}

.sidebar .nav-links li .sub-menu {
  padding: 6px 6px 14px 80px;
  margin-top: -10px;
  background: rgb(3.8181818182, 122.1818181818, 234.1818181818);
  display: none;
}

.sidebar .nav-links li.showMenu .sub-menu {
  display: block;
}

.sidebar .nav-links li .sub-menu a {
  color: #fff;
  font-size: 16.5px;
  padding: 5px 0;
  white-space: nowrap;
  opacity: 0.6;
  transition: all 0.3s ease;
}

.sidebar .nav-links li .sub-menu a:hover {
  opacity: 1;
}

.sidebar.close .nav-links li .sub-menu {
  position: absolute;
  left: 100%;
  top: -10px;
  margin-top: 0;
  padding: 10px 20px;
  border-radius: 0 6px 6px 0;
  opacity: 0;
  display: block;
  pointer-events: none;
  transition: 0s;
}

.sidebar.close .nav-links li:hover .sub-menu {
  top: 0;
  opacity: 1;
  pointer-events: auto;
  transition: all 0.4s ease;
}

.sidebar .nav-links li .sub-menu .link_name {
  display: none;
}

.sidebar.close .nav-links li .sub-menu .link_name {
  font-size: 18px;
  opacity: 1;
  display: block;
}

.sidebar .nav-links li .sub-menu.blank {
  opacity: 1;
  pointer-events: auto;
  padding: 3px 20px 6px 16px;
  opacity: 0;
  pointer-events: none;
}

.sidebar .nav-links li:hover .sub-menu.blank {
  top: 50%;
  transform: translateY(-50%);
}

.sidebar .profile-details {
  position: fixed;
  bottom: 0;
  width: 260px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #1d1b31;
  padding: 12px 0;
  transition: all 0.5s ease;
}

.sidebar.close .profile-details {
  background: none;
}

.sidebar.close .profile-details {
  width: 78px;
}

.sidebar .profile-details .profile-content {
  display: flex;
  align-items: center;
}

.sidebar .profile-details img {
  height: 52px;
  width: 52px;
  object-fit: cover;
  border-radius: 16px;
  margin: 0 14px 0 12px;
  background: #1d1b31;
  transition: all 0.5s ease;
}

.sidebar.close .profile-details img {
  padding: 10px;
}

.sidebar .profile-details .profile_name,
.sidebar .profile-details .job {
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  white-space: nowrap;
}

.sidebar.close .profile-details i,
.sidebar.close .profile-details .profile_name,
.sidebar.close .profile-details .job {
  display: none;
}

.sidebar .profile-details .job {
  font-size: 12px;
}

.home-section {
  position: relative;
  background: #E4E9F7;
  height: 100vh;
  left: 260px;
  width: calc(100% - 260px);
  transition: all 0.5s ease;
}

.sidebar.close ~ .home-section {
  left: 78px;
  width: calc(100% - 78px);
}

.home-section .home-content {
  height: 60px;
  display: flex;
  align-items: center;
}

.home-section .home-content .text {
  color: #11101d;
  font-size: 35px;
}

.home-section .home-content .text {
  font-size: 26px;
  font-weight: 600;
}

@media (max-width: 400px) {
  .sidebar.close .nav-links li .sub-menu {
    display: none;
  }
  .sidebar {
    width: 78px;
  }
  .home-section {
    left: 78px;
    width: calc(100% - 78px);
    z-index: 100;
  }
  .sidebar.close ~ .home-section {
    width: 100%;
    left: 0;
  }
}
